@import url(https://fonts.googleapis.com/css2?family=Lato&family=Jua&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', 'Jua', sans-serif;
  -webkit-tap-highlight-color: transparent;
}

html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
}

.home,
.reports,
.products,
.Commands,
.Team {
  display: flex;
  min-height: calc(100vh - 80px);
  align-items: center;
  justify-content: center;
}

.navbar {
  background-color: #23272A;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
}

.nav-menu {
  background-color: #23272A;
  width: 300px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
  z-index: 1000;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: #1a83ff;
  color: #f5f5f5;
}

.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background-color: #23272A;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

span {
  margin-left: 16px;
}

.dim {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, .5);
  z-index: 100;
}
.Desc {
    font-size: 1.5rem;
    color: white;
    text-align: center;
    font-family: 'Noto Sans KR', sans-serif;
}

.btn-container {
    display: flex;
}

.btn-container > a > button {
    outline: 0;
    padding: 2px 16px;
    background-color: #7289da;
    color: #fff;
    /*box-shadow: 0 4px 16px rgb(114, 137, 218);*/
    font-size: 14px;
    font-weight: 500;
    border-radius: 3px;
    width: auto;
    height: 32px;
    min-width: 60px;
    min-height: 32px;
    margin: 0 5px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    transition: background-color .17s ease,color .17s ease;
}


.btn-container > a > button > div {
    /*font-family: Whitney, "Apple SD Gothic Neo", NanumBarunGothic, "맑은 고딕", "Malgun Gothic", Gulim, 굴림, Dotum, 돋움, "Helvetica Neue", Helvetica, Arial, sans-serif;*/
    margin: 0 auto;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    font-family: 'Noto Sans KR', sans-serif;
}

.btn-container > a > button:focus {
    outline: 0;
}

.btn-container > a > button:hover {
    background-color: #677bc4;
}

.home { 
    background-color: #23272A;
    flex-direction: column;
}

.btn-container > a > button, .Img {
    -ms-user-select: none;
        user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -webkit-user-drag: none;
}

.Img {
    width: 256px;
    height: 256px;
}

@media (max-width: 700px) {
    .Img {
        width: 200px;
        height: 200px;
    }
}
.Commands { 
    background-color: #23272A;
    flex-flow: column wrap;
}

.CommandsCard {
    font-family: 'Noto Sans KR', sans-serif;
}
.Team { 
    background-color: #23272A;
    flex-flow: column wrap;
}

.Developer {
    color: white;
    font-weight: bold;
    font-size: 40px;
    font-family: 'Noto Sans KR', sans-serif;
}
